.profile_picture {
    border-radius: 100%;
}

.container {
    display: flex;
    justify-content: center;
    padding: 10px;
  }

  .profile_picture {
    width: 150px;
    height: 150px;
    margin-bottom: 50px;
    margin-top: 50px;
  }

  .textblock {
    padding: 20px;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 20px;
    width: 350px;
    margin: auto;
  }

  .left {
    color: var(--tg-theme-text-color);
    float: left;
    width: 50%;
    text-align: left;
  }
  
  .right {
    color: var(--tg-theme-button-color);
    float: right;
    width: 50%;
    text-align: right;
  }

  .overall {
    border: 1px solid var(--tg-theme-button-color);
  }